import { betterAuth } from "better-auth";

console.log('üîê Better Auth config loading...');
console.log('üîê Google Client ID:', process.env.GOOGLE_CLIENT_ID ? '‚úÖ Set' : '‚ùå Missing');
console.log('üîê Google Client Secret:', process.env.GOOGLE_CLIENT_SECRET ? '‚úÖ Set' : '‚ùå Missing');
console.log('üîê Auth Secret:', process.env.AUTH_SECRET ? '‚úÖ Set' : '‚ùå Missing');

export const auth = betterAuth({
    secrets: [process.env.AUTH_SECRET!],
    socialProviders: {
        google: {
            clientId: process.env.GOOGLE_CLIENT_ID!,
            clientSecret: process.env.GOOGLE_CLIENT_SECRET!,
        },
    },
    storage: {
        sessions: new Map<string, any>(),
        async getSession(id: string) {
            console.log('üîê Getting session:', id);
            return this.sessions.get(id);
        },
        async setSession(id: string, session: any) {
            console.log('üîê Setting session:', id, session);
            this.sessions.set(id, session);
        },
        async deleteSession(id: string) {
            console.log('üîê Deleting session:', id);
            this.sessions.delete(id);
        },
    },
});

console.log('üîê Better Auth configured with Map storage');
